import React from "react";
import {NavLink} from "react-router-dom";
import {useSelector} from "react-redux";

export const userTag = (user) => {
    return <p className={'userTag'}>{user.first_name} {user.last_name}</p>

}

const MainTitle = () => {
    const user = useSelector(state => state.user)

    return (
    <div className={'mainTitle'}>
        {user.status === ' ' && <div className={'mainTitle__actions actions'}>
            <NavLink className={'link'} to={'auth'}></NavLink>
            <p className={'actions__separator'}>/</p>
            <NavLink className={'link'} to={'/registerUser'}></NavLink>
        </div>}
        {user.status === '' && <>
            {userTag(user)}
        </>}
        <h1 className="mainTitle__title">    </h1>
        <p className="mainTitle__description">Transfer Abkhazia -     
              .                 .</p>
        <NavLink className={'register'} to={'/registerUser'}> </NavLink>
    </div>
    )
}

export default MainTitle